<script>
// 引入swiper组件
// import { Swiper, SwiperSlide } from 'vue-awesome-swiper';
// import 'swiper/swiper-bundle.css';
// 引入通用table组件
import { ISANDON, MAIN_DEVICE_INFO_JUMP } from '@/utils/ajaxConfigure';
// 引入标题组件
export default {
  components: {
    // Swiper,
    // SwiperSlide
  },
  props: {
    groupData: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  data() {
    return {
      MAIN_DEVICE_INFO_JUMP: MAIN_DEVICE_INFO_JUMP,
      color: ['', '#01CC9F', '#FFCC00', '#999999', '#E90000'],
      swiperOptions: {
        slidesPerView: 10,
        spaceBetween: 10,
        freeMode: true
      },
      isAndon: ISANDON
    };
  },
  created() {
    // 调用列表查询接口
    this.update();
  },
  methods: {
    /**
     * @description:处理数据
     * @param
     * @return:
     * @author: CH
     */
    update() {
      this.groupData.oeeNum = this.getNum(this.groupData.oee);
      this.groupData.timeUtilizationRateNum = this.getNum(
        this.groupData.timeUtilizationRate
      );
      this.groupData.performanceUtilizationRateNum = this.getNum(
        this.groupData.performanceUtilizationRate
      );
      this.groupData.yieldRateNum = this.getNum(this.groupData.yieldRate);
    },
    // 处理百分比
    getNum(val) {
      if (val === '0') {
        return '0';
      } else {
        return (val * 100).toFixed(0) + '%';
      }
    },
    pageJump(param) {
      var that = this;
      // this.$store.state.deviceIsJump = true;
      // this.$store.state.deviceJump = param;
      // that.$store.state.monitorTags = new Date().getTime();
      // localStorage.setItem('deviceJump', JSON.stringify(param));
      // this.$emit('jupm');

      if (MAIN_DEVICE_INFO_JUMP) {
        if (this.isAndon) {
          that.$router.push('/equipment/equipmentStatus_andon?id=150&type=deviceJump');
        } else {
          that.$router.push('/equipment/deviceStatusInfo?type=deviceJump');
          // this.$emit('jupm');
        }
        // that.$store.state.monitorTags = new Date().getTime();
        // if (this.isAndon) {
        //   that.$router.push('/equipment/equipmentStatus_andon?id=150');
        // } else {
        //   this.$emit('jupm');
        // }
      } else {
        this.$store.state.deviceIsJump = true;
        this.$store.state.deviceJump = param;
        that.$store.state.monitorTags = new Date().getTime();
        localStorage.setItem('deviceJump', JSON.stringify(param));
        this.$emit('jupm');
      }

      // this.$store.state.deviceIsJump = true;
      // this.$store.state.deviceJump = param;

      // console.log(555, param);

      // var that = this;
      // // 指定跳转的地址
      // // this.$router.replace('/equipment/equipmentStatus?id=33&jump=true');

      // // // 指定跳转的路由的名字下
      // // this.$router.replace({name:'指定路由名称'})

      // 通过push进行跳转

      // that.$store.state.monitorTags = new Date().getTime();
      // if (this.isAndon) {
      //   that.$router.push('/equipment/equipmentStatus_andon?id=150&type=deviceJump');
      // } else {
      //   that.$router.push('/equipment/deviceStatusInfo?type=deviceJump');
      //   // this.$emit('jupm');
      // }
      //   that.$store.state.monitorTags = new Date().getTime();
      //   if (this.isAndon) {
      //     that.$router.push('/equipment/equipmentStatus_andon?id=150');
      //   } else {
      //     this.$emit('jupm');
      //   }
      // }, 0);
    },
    strFunc(val) {
      if (val === undefined || val === 'undefined') {
        return '';
      } else {
        if (isNaN(val)) {
          return val;
        } else {
          return Number(Number(val).toFixed(5));
        }
      }
    },
    keepChineseCharactersOnly(str) {
      // 使用正则表达式匹配中文字符
      var pattern = /[\u4e00-\u9fa5]/g;

      // 使用正则表达式的 exec 方法来匹配字符串中的中文字符
      var matches = str.match(pattern);

      // 如果没有匹配到中文字符，返回空字符串
      if (!matches) {
        return '';
      }

      // 将匹配到的中文字符数组转换为字符串并返回
      return matches.join('');
      // return str;
    }
  }
};
</script>

