import { postAplicationJSON } from '@/utils/request';
// 我的主控制室生产任务单设备状态效率查询
export const getMainControlRoomStatisticsAction = (param, jsonData) =>
  postAplicationJSON('deviceStateScale/getMainControlRoomStatistics.action?', param, jsonData);
// 所有的车间以及车间下的产线的信息
export const getAllWorkshopInformationAction = (param, jsonData) =>
  postAplicationJSON('deviceStateScale/getAllWorkshopInformation.action?', param, jsonData);
// 延期的任务单
export const getExtensionProductionTaskAction = (param, jsonData) =>
  postAplicationJSON('deviceStateScale/getExtensionProductionTask.action?', param, jsonData);
