<template>
  <el-container>
    <!-- <el-header>
      <header-title :flag="false" />
    </el-header> -->
    <el-main class="border-top-common">
      <div style="margin: 10px; display: flex; flex-wrap: wrap; column-gap: 5px; row-gap: 5px;">
        <div v-for="func in commonFunctions" :key="func.menuId">
          <div
            v-if="menuDeal($store.state.pageInfo.menuList).includes(func.menuId)"
            style="margin-left:10px;text-align:center;height: 80px;width: 80px;display: flex; flex-direction: column;justify-content: center; cursor: pointer;"
            class="func-btn"
            @click="goToPage(func.url,func.menuId, func.menuCnName)"
          >
            <i class="el-icon-menu"></i>
            <span style="line-height: 30px;white-space:nowrap;overflow: hidden;">{{ func.menuCnName }}</span>
          </div>
        </div>
        <div
            style="width: 80px; height: 80px;display: flex; flex-direction: column; justify-content: center; cursor: pointer;overflow: hidden; align-items: center;"
            class="func-btn"
            @click="goToPage('/setCommonFunctions')"
          >
            <i class="el-icon-plus"></i>
            <span style="line-height: 30px;">添加</span>
          </div>
      </div>
    </el-main>
  </el-container>
</template>

<script>

import { showErrorMessage } from '@/utils/message';
import { getCommonUseFunctions } from '@/api/base';
import { isNonemptyArray } from '@/utils/array';
import { DNC_URL, MES_URL } from '@/utils/ajaxConfigure';
export default {
  name: 'roomIndex',
  components: {

  },

  data() {
    return {
      myWorkTitle: '我的工作',
      myTableTitle: '我的分析',
      commonFunctions: []
    };
  },

  mounted() {
    this.getCommonFunctions();
    // this.queryMessagePushPage();
  },

  methods: {

    /**
     * @description: 点击常用功能，跳转对应页面
     * @param {string} url 页面地址
     * @author: cyb
     */
    goToPage(url, id, menuName) {
      if (menuName === 'DNC系统') {
        const token = localStorage.getItem('token') || '';
        // const url = `${DNC_URL}?key=${token}`;
        window.open(DNC_URL, token);
      } else if (menuName === 'MES系统') {
        // eslint-disable-next-line no-redeclare
        var token = '';
        if (localStorage.getItem('mesToken') === 'undefined') {
          window.open(MES_URL);
        } else {
          // eslint-disable-next-line no-redeclare
          var tokenHeader = localStorage.getItem('mesTokenHeader');
          token = localStorage.getItem('mesToken');
          window.open(MES_URL + '?tokenHead=' + tokenHeader + '&token=' + token, token);
        }
        // const url = `${DNC_URL}?key=${token}`;
      } else if (menuName === '车间看板') {
        window.open('/kanban?cid=' + localStorage.getItem('companyId'));
      } else if (menuName === '安灯车间看板') {
        window.open('/kanban_andon?cid=' + localStorage.getItem('companyId'));
      } else {
        if (id) {
          this.$router.push({
            path: url,
            query: {
              id
            }
          });
        } else {
          this.$router.push(url);
        };
      }
    },
    async getCommonFunctions() {
      try {
        const res = await getCommonUseFunctions({});
        if (isNonemptyArray(res.rows)) {
          this.commonFunctions.push(...res.rows);
        }
      } catch (error) {
        showErrorMessage(error);
      };
    },
    menuDeal(data) {
      const menuData = [];
      data.forEach(item => {
        if (item.children && item.children.length > 0) {
          menuData.push(...item.children);
        }
      });
      return menuData.map(item => item.entity.id);
    }
  }
};
</script>

<style lang="less" scoped>
.text-overflow {
  font-weight: bold;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: pointer;
}
.text-overflow :hover{
  color:rgb(38, 147, 237);
}
.container {
  background: #ededed;
  padding: 5px 20px;
  min-height: calc(100vh - 113px);
}

.work-title-row {
  display: flex;
  justify-content: space-between;
}

.my-work-title {
  background: transparent;
}

.function-row,
.table-row {
  display: flex;
  margin-left: 10px;
  // column-gap: 10px;
  // -webkit-column-gap: 10px;
  // -moz-column-gap: 10px;
}

.function-card {
  flex: 1;
  margin-right: 10px;

  /deep/ .el-card__header {
    padding: 0;
    line-height: 40px;
  }

  /deep/ .el-card__body {
    padding: 0;
  }
}

.todo-card,
.message {
  width: 300px;

  /deep/ .el-card__header {
    padding: 0;
    line-height: 40px;
  }

  /deep/ .el-card__body {
    padding: 0;
  }

  .todo-list {
    list-style: none;
    padding-left: 0;
    margin: 20px 0;

    li {
      text-align: left;
      margin-left: 20px;
      line-height: 32px;
    }
  }
}

.table-title-row {
  margin-top: 10px;
}

.table {
  flex: 3 0 auto;
  display: flex;
  column-gap: 10px;
}

.func-btn {
  width: 70px;
  height: 70px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  cursor: pointer;
}

.func-btn:hover {
  background: #ededed;
}
</style>
