    // console.log('nihaoya');
    // alert('nihaoya');
  },
  methods: {
    openGet() {
      this.getAllWorkshopInformation();
      this.timer = setInterval(() => {
        this.getAllWorkshopInformation();
      }, 10000);
    },
    closeGet() {
      clearInterval(this.timer);
      this.timer = null;
    },
    back() {
      // this.$router.push('/MainControlRoom');
      this.showPage = 0;
      this.$store.state.deviceIsJump = false;
    },
    jupm(param) {
      this.showPage = 1;
    },
    async getGroupTree() {
      // 若是车间分组
      // 执行调用接口
      try {
        const res = await deviceGroupGetTreeFunc();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.groupOptions = res.rows;
        } else {
          // 提示异常信息
          // showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 头部按钮事件
     * @param {*} type
