     */
    buttonClickEvent(type) {
      // if (type === 'goBack') {
      // this.$router.go(-1);
      // return;
      this.showPage = 0;
      // }
      // if (type === 'exit') {
      //   this.currentPage = 'main';
      //   return;
      // }
      // this[`${type}`]();
    },
    /**
     * @description: 刷新表格数据
     * @author: cyb
     */
    refreshTableData(savedSearchCondition) {

    },
    /**
     * @description:获取基础数据
     * @param
     * @return:
     * @author: CH
     */
    async getMainControlRoomStatistics() {
      try {
        const res = await getMainControlRoomStatisticsAction({}, {});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.taskInfo.boxsData[0].value = res.rows.taskNoStartCount;
          this.taskInfo.boxsData[1].value = res.rows.taskProcessingCount;
          this.taskInfo.boxsData[2].value = res.rows.taskExtensionCount;
          this.taskInfo.boxsData[3].value = res.rows.taskCompletedCount;
          this.productionInfo.boxsData[0].value = res.rows.planCount;
          this.productionInfo.boxsData[1].value = res.rows.completeCount;
          this.productionInfo.boxsData[2].value = res.rows.inspectCount;
          this.productionInfo.boxsData[3].value = res.rows.ngCount;
          this.deviceInfo.boxsData[0].value = res.rows.runCount;
          this.deviceInfo.boxsData[1].value = res.rows.idleCount;
          this.deviceInfo.boxsData[2].value = res.rows.warnCount;
          this.deviceInfo.boxsData[3].value = res.rows.offLineCount;
          this.UtilizationRateinfo.boxsData[0].value =
            (res.rows.oee * 100).toFixed(0) + '%';
          this.UtilizationRateinfo.boxsData[0].color = this.getColor(
            res.rows.oee
          );
          this.UtilizationRateinfo.boxsData[1].value =
            (res.rows.timeUtilizationRate * 100).toFixed(0) + '%';
          this.UtilizationRateinfo.boxsData[1].color = this.getColor(
            res.rows.timeUtilizationRate
          );
          this.UtilizationRateinfo.boxsData[2].value =
            (res.rows.performanceUtilizationRate * 100).toFixed(0) + '%';
          this.UtilizationRateinfo.boxsData[2].color = this.getColor(
            res.rows.performanceUtilizationRate
          );
          this.UtilizationRateinfo.boxsData[3].value =
            (res.rows.yieldRate * 100).toFixed(0) + '%';
          this.UtilizationRateinfo.boxsData[3].color = this.getColor(
            res.rows.yieldRate
          );
        } else {
          // 提示异常信息
          // showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    reset() {
      this.deviceName = '';
      this.groupId = null;
      this.getAllWorkshopInformation();
    },
    /**
     * @description:获取车间信息
     * @param
     * @return:
     * @author: CH
     */
    async getAllWorkshopInformation() {
      // this.groupData = [];
      var param = {
        deviceLikeName: this.deviceName
      };
      if (this.groupId && this.groupId.length > 0) {
        param.groupId = this.groupId[this.groupId.length - 1];
      }
      try {
        const res = await getAllWorkshopInformationAction(param, {});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          console.log(MES_CONFIG);
          console.log(localStorage.getItem('company'));
          console.log(MES_CONFIG[localStorage.getItem('company')]);
          if (MES_CONFIG[localStorage.getItem('companyId')]) {
            this.mesItem = true;
            // this.groupData = res.rows;
            this.groupDataBf = res.rows;
            this.togetMdcDeviceInfoPage();
          } else {
            this.mesItem = false;
            this.groupData = res.rows;
          }
        } else {
          // 提示异常信息
          // showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    async togetMdcDeviceInfoPage() {
      var param = {
        size: 99999999,
        current: 1
      };
      try {
        const res = await getMdcDeviceInfoPage(param, {});
        console.log('lalala', res);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          for (var i = 0; i < this.groupDataBf.length; i++) {
            // console.log(this.groupData[i]);
            for (var x = 0; x < this.groupDataBf[i].lineDeviceInformationVoList.length; x++) {
              var line = this.groupDataBf[i].lineDeviceInformationVoList[x];
              for (var y = 0; y < line.deviceLiveVoList.length; y++) {
                for (var z = 0; z < res.rows.length; z++) {
                  if (res.rows[z].deviceCode === line.deviceLiveVoList[y].deviceCode) {
                    if (res.rows[z].dispatched) {
                      console.log('有生产', res.rows[z].dispatched);
                      this.groupDataBf[i].lineDeviceInformationVoList[x].deviceLiveVoList[y].itemName = res.rows[z].dispatched.itemName;
                      this.groupDataBf[i].lineDeviceInformationVoList[x].deviceLiveVoList[y].itemModel = res.rows[z].dispatched.itemModel;
                      console.log(this.groupDataBf[i].lineDeviceInformationVoList[x].deviceLiveVoList[y]);
                    }
                  }
                }
              }
            }
          }
          // this.$nextTick(() => {
          this.groupData = { ...this.groupDataBf };
          // });

          // this.togetMdcDeviceInfoPage();
        } else {
          // 提示异常信息
          // showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description:判断显示颜色
     * @param
     * @return:
     * @author: CH
     */
    getColor(val) {
      if (val < 0.6) {
        return '#E90000';
      } else if (val > 0.6 && val < 0.8) {
        return '#FFCC00';
      } else if (val > 0.8) {
        return '#0871DC';
      }
    },
    getUrlKeyMain(name, url) {
      // eslint-disable-next-line no-sparse-arrays
      return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(url) || [, ''])[1].replace(/\+/g, '%20')) || null;
    }
